import express from 'express';
const authapp = express.Router();
import verfy_admin from '../middlewares/check_admin';
import verfy_token from '../middlewares/check_token';
import userController from '../controllers/usersController';
import additionallyController from '../controllers/additionally';
import sessionController from '../controllers/sessionsController';
import verfy_admin_and_mentee from '../middlewares/check_admin_and_mentee';
import verfy_admin_and_mentor from '../middlewares/check_admin_and_mentor';



authapp.post(`/signup`, userController.signup)
       .post(`/signin`, userController.signin) 
       .get(`/admins/:adminId`,verfy_token, /*verfy_admin,*/ userController.View_admin)
       .get(`/mentors/:mentorId`,verfy_token, /*verfy_admin,*/ userController.View_mentor)
       .get(`/mentees/:menteeId`,verfy_token, /*verfy_admin,*/ userController.View_mentee)
       .get(`/users`,verfy_token, /*verfy_admin_and_mentee,*/userController.view_all_users)
       .get(`/admins`, verfy_token, /*verfy_admin_and_mentor,*/ userController.View_all_admin)
       .get(`/mentors`, verfy_token, /*verfy_admin_and_mentor,*/ userController.View_all_mentor)
       .get(`/mentees`, verfy_token, /*verfy_admin_and_mentor,*/ userController.View_all_mentee)

       
       .post(`/session`,verfy_token, sessionController.create_session)
       .get(`/session`,verfy_token, sessionController.view_all_sessions)
       .patch(`/sessions/:sessionId/accept`, sessionController.accept_session)
       .patch(`/sessions/:sessionId/reject`, sessionController.reject_session)
       .get(`/sessions/:sessionId`,verfy_token, sessionController.view_session_by_sessionId)
       .get(`/sessionsmentor/:mentorId`,verfy_token, sessionController.view_sessions_of_mentor)
       .get(`/sessionsmentee/:menteeId`,verfy_token, sessionController.view_sessions_of_mentee)
       .get(`/sessions`,verfy_token, sessionController.view_sessions_by_mentee_or_mentor_by_token_id_info)
       
       .get(`/reviews`,verfy_token, additionallyController.view_all_review)
       .patch(`/user/:userId`, additionallyController.update_profile)
       .patch(`/password/:userId`, additionallyController.update_password)
       .post(`/sessions/:sessionId/review`, additionallyController.review_mentor)
       .delete(`/sessions/:sessionId/review`, additionallyController.delete_review)
       .patch(`/users_mentor_or_mentee/:userId`, additionallyController.change_user_to_mentor);





export default authapp;